___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
  
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  background-color: black;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

input {
  padding: 5px;
  border-radius: 3px;
}
button {
   color: white;
  background-color: purple;
  padding: 9px;
  border-radius: 7px;
  cursor: pointer;
  border: none;
}

button:hover {
  opacity: 0.7;
  color: black;
}


.container h1 {
  text-align: center;
  margin-bottom: center;
}

.cpf-gerado {
  font-size: 2em;
  color: purple;
  text-align: center;
  font-weight: bold;
  margin-bottom: 40px;
  letter-spacing: 3px;
}

.error {
  font-size: 20px;
  color: red;
}

.ok {
  font-size: 20px;
  color: rgb(5, 201, 93);
}
